.footer {
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.footer-item {
    display: flex;
    margin-left: 30px;
    padding: 50px;
    text-decoration: none;
}